<template>
       <el-dialog
            title=""
            :visible.sync="emailDialog"
            width="30%">
            <el-form :inline="true" :model="emailConfigList" ref="emailConfigList" :rules="rules" :label-position="labelPosition" label-width="80px" size="small">
                <el-form-item label="" prop="emailTeam">
                    <el-select style="width:250px" v-model="emailConfigList.emailTeam">
                        <el-option
                                    v-for="item in versionList"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="serverAddress">
                    <el-input style="width:250px"  v-model="emailConfigList.serverAddress"  >
                    </el-input>
                </el-form-item>
                <el-form-item label="" prop="account">
                    <el-input style="width:250px" v-model="emailConfigList.account" >
                    </el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input style="width:250px" v-model="emailConfigList.password" show-password>
                    </el-input>
                </el-form-item>
                <el-form-item label="" prop="fromAddr">
                    <el-input style="width:250px" v-model="emailConfigList.fromAddr" >
                    </el-input>
                </el-form-item>
                <el-form-item label="" prop="toAddr">
                    <el-input style="width:250px" v-model="emailConfigList.toAddr" type="textarea" :rows="4">
                    </el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <!-- <el-button type="primary" @click="resetForm('emailConfigList')" size="mini"> </el-button> -->
                <el-button @click="emailDialog = false" size="mini"> </el-button>
                <el-button type="primary" size="mini"  @click="emailConfig('emailConfigList')"> </el-button>
            </span>
        </el-dialog>
</template>
<style scoped>
.el-dialog__footer{
    text-align: center;
}
.el-form-item{
    margin-bottom: 20px;
}
</style>
<script>
import bus from '../../bus'
import qs from 'qs'
export default {
    data(){
        return{
            labelPosition:'left',
            emailDialog:false,
            freshTable:true,
            versionList:[],
            emailConfigList:{
                emailId:'',
                emailTeam:'',
                serverAddress:'',
                account:'',
                password:'',
                fromAddr:'',
                toAddr:'',
                ccAddr:'',
                action:''
            },
            rules:{
                emailTeam:[
                    {required:true,message:'',trigger: 'change'}
                ],
                serverAddress:[
                    {required:true,message:'',tigger:'blur'}
                ],
                account:[
                    {required:true,message:'',tigger:'blur'}
                ],
                password:[
                    {required:true,message:'',tigger:'blur'}
                ],
                fromAddr:[
                    {required:true,message:'',tigger:'blur'}
                ],
                toAddr:[
                    {required:true,message:'',tigger:'blur'}
                ],
            }
        }
    },
    methods:{
        //
        resetForm(emailConfigList){
            console.log("")
        },
        //-
        delConfig(val){
            this.emailConfigList=val
            this.emailConfigList.action='del'
            this.emailConfigList.emailId=val.email_id
            this.$axios.post(this.$api.emailConfig,qs.stringify(this.emailConfigList))
                .then(response=>{
                    console.log(response.data)
                    if(response.data.code==0){
                        this.$message({
                            showClose:true,
                            message:response.data.msg,
                            type:"success"
                        })
                        bus.$emit('freshTable',this.freshTable)
                    }else{
                        this.$message({
                            showClose:true,
                            message:"Fail",
                            type:"fail"
                        })
                    }
                })
        },
        //--
        emailConfig(emailConfigList){
                this.$refs[emailConfigList].validate((valid)=>{
                    if (valid){
                        console.log('email',this.emailConfigList)
                        if(this.emailConfigList.emailId==''){
                            this.emailConfigList.action='add'
                        }else{
                            this.emailConfigList.action='edit'
                        }
                       this.$axios.post(this.$api.emailConfig,qs.stringify(this.emailConfigList))
                       .then(response=>{
                           console.log(response.data)
                           if(response.data.code==0){
                                this.$message({
                                    showClose:true,
                                    message:response.data.msg,
                                    type:"success"
                                })
                                bus.$emit('freshTable',this.freshTable)
                            }else{
                                this.$message({
                                    showClose:true,
                                    message:"Fail",
                                    type:"fail"
                                })
                            }
                            this.emailDialog=false
                       })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }

                })
        },
        //
        selectVersionInfomation(){
            this.$axios.get(this.$api.selectVersionData)
            .then(response=>{
                this.versionList=response.data
            })
        }
    },
    mounted(){
        
    },
    created(){
        this.selectVersionInfomation();
        console.log('',this.emailConfigList)
        bus.$on('emailDia',msg=>{
            this.emailConfigList={
                emailId:'',
                emailTeam:'',
                serverAddress:'',
                account:'',
                password:'',
                fromAddr:'',
                toAddr:'',
                ccAddr:'',
                action:''
            }
            // this.$refs[this.emailConfigList].resetFields();
            this.emailDialog=msg
        })
        bus.$on('editEmailData',msg=>{
            this.emailDialog=true
            this.emailConfigList.emailId=msg.email_id
            this.emailConfigList.emailTeam=msg.Modelversion_id
            this.emailConfigList.serverAddress=msg.email_serverAddress
            this.emailConfigList.fromAddr=msg.email_fromAddr
            this.emailConfigList.account=msg.email_account
            this.emailConfigList.password=msg.email_password
            this.emailConfigList.toAddr=msg.email_toAddr
        })
        bus.$on('delEmailData',msg=>{
            this.delConfig(msg)
        })
    }
}
</script>


